import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			goods: [],
			id:0,
			categ:[]
		}
	},
	onLoad(options) {
		this.id=options.id?options.id:0;
		this.loadData();
	},
	onShow() {
		if (!app.globalData.user_info) {
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		loadData() {
			this.loadCateg();
			this.loadGoods(this.id);
		},
		loadCateg(){
			this.request({
				url: interfaces.categList,
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.categ=res.data;
					}
				}
			})
		},
		loadGoods(id){
			this.request({
				url: interfaces.getCategGoods,
				data:{
					id:id
				},
				method:'post',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						this.goods=res.data;
					}
				}
			})
		},
		checkGoods(id){
			wx.navigateTo({
				url: 'select?id='+id,
			})
		}
		
		
	}
}
